/* WARNING: This site.css is a copy of /ko/assets/css/site.scss DO NOT modify this file unless you are syncing from the original file. */

@import "colours";
@import "layout";
@import "media-query-variables";

body {
    position: relative;
    padding-top: 67px;
    padding-bottom: 155px;
    min-height: 100vh;

    @include md-phone-up {
        padding-bottom: 136px;
    }

    @include lg-phone-up {
        padding-bottom: 117px;
    }

    @include tablet-portrait-up {
        padding-bottom: 91px;
    }

    @include desktop-up {
        padding-top: 83px;
    }
}

.navbar-brand {
    img {
        height: 25px;
    }
}

.footer-brand {
    img {
        height: 18px;
    }
}

.btn-primary-pocu {
    color: $white !important;
    background-color: $pocu-primary-colour !important;

    &:hover:not([disabled]) {
        color: $light-grey !important;
        background-color: $pocu-primary-colour-dark !important;
    }
}

.site-footer {
    bottom: 0;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 100%;
    border-bottom: 6px solid $pocu-primary-colour;
    font-size: small;
}

.shadow--md {
    @include tablet-portrait-up {
        box-shadow: 0 .5rem 1rem rgba(0,0,0,.15) !important;
    }
}

.border--md {
    @include tablet-portrait-up {
        border: 1px solid $light-grey !important;
    }
}

.opacity-75 {
    opacity: 0.75 !important;
}

.article-link-box {
    display: block;
    color: $black;

    &:hover {
        text-decoration: none;
        opacity: 0.7;
        color: $black;
    }
}

.tag-link {
    color: $black;
    opacity: 0.80;

    &:hover {
        color: $black;
        opacity: 1;
    }
}

.w-lg-75 {
    @include desktop-up {
        width: 75% !important;
    }
}

.vertical-divider {
    position: relative;

    &:after {
        content: "";
        width: 1px;
        height: 70%;
        position: absolute;
        right: 0px;
        top: 15%;
        background-color: $light-grey;
    }
}

.display-lg-list-item {
    @include desktop-up {
        display: list-item !important;
    }
}

.text-wrap {
    white-space: normal;
}


.faq-accordion::before {
    display: block;
    content: " ";
    margin-top: -$body-padding-top;
    height: $body-padding-top;
    visibility: hidden;
    pointer-events: none;

    @include desktop-up {
        margin-top: -$body-padding-top-desktop-up;
        height: $body-padding-top-desktop-up;
    }
}

table {
    min-width: 100%;
    border: 1px solid black;

    @include desktop-up {
        min-width: 75%;
    }

    th, td {
        padding: 5px;
        border: 1px solid black;
    }
}

.welcome-to-pocu {
    a {
        max-width: 600px;
        text-decoration: none !important;
        color: black;
        box-shadow: 0 .5rem 1rem rgba(0,0,0,.15) !important;
    }

    img {
        min-width: 140px;
        min-height: 140px;
        width: 140px;
        height: 140px;
        padding: 5px;
    }

    .headlines {
        font-size: 1rem;
        line-height: 1.5rem;
    }

    .description {
        word-break: keep-all;
        font-size: 0.9rem;
    }
}

/* WARNING: Below typography is a copy of the typography in \Shop\wwwroot\css\site.css. DO NOT modify this css unless you are syncing from the original file. */
/* typography for documents. */

.doc {
    font-size: 1rem;
    line-height: 1.7;
    letter-spacing: -0.004em;
    word-break: keep-all;
    overflow-wrap: break-word;

    img {
        max-width: 100%;
    }

    blockquote {
        padding: 0 1em;
        color: rgba(0,0,0,0.5);
        border-left: 0.25em solid #dfe2e5;
    }
}

    .doc p {
        margin-top: 1em;
        margin-bottom: 1em;
    }

    .doc * + h1 {
        margin-top: 0.67em;
    }

    .doc * + h2 {
        margin-top: 0.83em;
    }

    .doc * + h3 {
        margin-top: 1em;
    }

    .doc * + h4 {
        margin-top: 1.33em;
    }

    .doc p + h1, .doc p + h2, .doc p + h3, .doc p + h4 {
        margin-top: 2rem;
    }

    .doc h1, .doc h2, .doc h3, .doc h4 {
        line-height: 1.5;
        margin-bottom: 0.75rem;
        font-weight: 700;
    }

    .doc h1 {
        font-size: 2.25rem;
    }

    .doc h2 {
        font-size: 1.75rem;
    }

    .doc h3 {
        font-size: 1.25rem;
    }

    .doc h4 {
        font-size: 1rem;
    }

    .doc h5 {
        font-size: 0.83em;
        font-weight: 700;
        margin-top: 1.67em;
        margin-bottom: 1.67em;
    }

    .doc h6 {
        font-size: 0.67em;
        font-weight: 700;
        margin-top: 2.33em;
        margin-bottom: 2.33em;
    }

    /* loose list */
    .doc li p {
        margin: 0 !important;
    }
    
    .doc ol, 
    .doc ul {
        margin: 0.5em 0;
    }
    
    .doc li {
        margin: 0 0 0.5em;
    }

    .doc .tight-list ol,
    .doc .tight-list ul,
    .doc .tight-list li {
        margin: 0;
    }

@media (min-width: 768px) {
    .doc {
        font-size: 1.125rem;
    }

        .doc p + h1, .doc p + h2, .doc p + h3, .doc p + h4 {
            margin-top: 2.5rem;
        }

        .doc h1, .doc h2, .doc h3, .doc h4 {
            margin-bottom: 1rem;
        }

        .doc h1 {
            font-size: 2.5rem;
        }

        .doc h2 {
            font-size: 2rem;
        }

        .doc h3 {
            font-size: 1.5rem;
        }

        .doc h4 {
            font-size: 1.125rem;
        }
}

.font-size-inherited {
    font-size: inherit;
}
